.Featured {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.featureItems {
  flex: 1;
  margin: 0px 20px;
  padding: 30px;
  border-radius: 10px;
  cursor: pointer;
  /* box shadow css generator<- google it*/
  /*https://html-css-js.com/css/generator/box-shadow*/
  -webkit-box-shadow: 0px 1px 11px -1px rgba(0, 0, 0, 0.53);
  box-shadow: 0px 1px 11px -1px rgba(0, 0, 0, 0.53);
}

.featuredTitle {
  font-size: 20px;
}

.featuredMoneyContainer {
  margin: 10px 0px;
  display: flex;
  align-items: center;
}

.featuredMoney {
  font-size: 30px;
  font-weight: 600;
}

.featuredMoneyRate {
  display: flex;
  align-items: center;
  margin-left: 20px;
}

.featuredIcon {
  font-size: 14px;
  margin-left: 5px;
  color: green;
}

.featuredIcon.negative {
  color: red;
}

.featuredSub {
  font-size: 15px;
  color: gray;
}

